import { createComponentFromModel } from 'core/loadEntities';

export default function ennemyLoopRightLeft(entity) {
  const forces = {
    name: 'forces',
    scope: 'common',
    params: {
      parts: {
        moveRight: {
          x: 250,
          y: 0,
          z: 0,
          rotateX: 0,
          rotateY: 0,
          duration: 3000,
          easing: {
            scope: 'ease',
            name: 'ease-linear',
          },
          ending:{
            scope: 'aJam',
            name: 'timeoutLoopLeftRight',
          },
          elapsed: 0,
        },
      },
    },
  };
  const newForces = createComponentFromModel.call(this, forces);

  const entityForces = entity.get('forces');
  delete entityForces.parts.moveLeft;

  const spriteSheets = entity.get('sprites');
  spriteSheets.parts.main.animation.frame=0;
  spriteSheets.parts.main.source = 'ennemy-walk-right'
  spriteSheets.parts.light.source = 'light-idle-right'
  spriteSheets.parts.light.destination.x= 72;
  entityForces.parts.moveRight = newForces.parts.moveRight;
  const hitbox = entity.get('hitbox');
  hitbox.x=64;
}
